import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import * as DongtaiActions from '../../actions/DongtaiActions'
import {Button, RefreshControl, ListView, NavBar, Icon, Toast} from 'antd-mobile'
import add from '../../../imgs/dongtai/add.png'
import back from '../../../imgs/common/back.png'
import  './index.less'
import DongtaiItem from '../../components/DontaiItem/index'


class Dongtai extends Component{


   _add = ()=>{
    const { actions } = this.props;
    actions.increment();

       // try {
       //     window.dsBridge.call("testReact", {"msg": "hello"}, function (responseData) {
       //         alert("callback from native-->" + responseData);
       //     });
       // }catch (error){}
   };

    _renderRow = (rowData, sectionID, rowID)=>(
        <DongtaiItem
            item= {rowData}
            onClick= {this._onItemClicked}
        />
    )
    _onItemClicked = (item)=>{
        alert(item.name)
    }
    _renderSpace = (sectionID, rowID) => (
        <div
            key={`${sectionID}-${rowID}`}
            style={{
                backgroundColor: '#F5F5F9',
                height: 8,
                borderTop: '1px solid #ECECED',
                borderBottom: '1px solid #ECECED',
            }}
        />
    );
    _renderFooter = ()=>{
        const { state } = this.props;
        return(  <div style={{ padding: 30, textAlign: 'center' }}>
            {state.canLoad ? '加载中...' : '已全部加载完毕'}
        </div>);
    }

    _onRefresh = () => {
        const { actions } = this.props;
        console.log('onRefresh执行');
        if (!this.manuallyRefresh) {
            actions.refreshIng();
        } else {
            this.manuallyRefresh = false;
        }
        actions.getAllTrends(false, 1);
    };
    _onLoad = () => {
        const {state, actions } = this.props;
        if(state.loading || !state.canLoad){
            return;
        }
        actions.loadIng();
        if(state.canLoad){
            console.log('onLoad执行');
            actions.getAllTrends(true, state.pageSize+1);
        }else
            console.log('onLoad未执行');
    };

    // 在更新发生后被立即调用。这是一个react组件更新完成后的通知。
    // componentDidUpdate(nextProps, nextState) {
    //     const {state} = this.props;
    //     if(state.error){
    //         Toast.fail(state.errorInfo, 1);
    //     }
    // }

    // 和安卓的OnCreate相似，只调用一次
    // 这是react组件的生命周期之一，在挂载发生后被立即调用。 需要DOM node的初始化应该放在这里。如果你要获取真的dom结构，应该是写在componentDidMount之后的任何时间内
    componentDidMount(){
        console.log("componentDidMount")
        const { actions } = this.props;
        this.manuallyRefresh = true;
        actions.refreshIng(); // refreshing = true 会触发onRefresh
    }

    render(){
        const { state, actions } = this.props;
        return(
            <div>
                <NavBar
                    iconName={null}
                    leftContent={(
                    <div style={{display:'flex',alignItems:'center',justifyContent:'center'}}>
                        <img style={{marginLeft:-20,width:'0.5rem',height:'0.5rem'}} src={back}/>
                        <span className="color-blue font">返回</span>
                    </div>
                    )}
                    mode="light"
                    onLeftClick={() => console.log('onLeftClick')}
                    rightContent={(
                        <img onClick={()=>{alert(1)}} src={add}/>

                    )}
                >全部动态</NavBar>

                <ListView
                    dataSource={state.dataSource.cloneWithRows(state.rows)}
                    renderRow={this._renderRow}
                    renderSeparator={this._renderSpace}
                    renderFooter={this._renderFooter}
                    initialListSize={5}
                    pageSize={5}
                    scrollRenderAheadDistance={200}
                    scrollEventThrottle={20}
                    style={{
                        height: document.documentElement.clientHeight,
                        border: '1px solid #ddd',
                        margin: '0.1rem 0',
                    }}
                    // onEndReached={this._onLoad}
                    scrollerOptions={{ scrollbars: true }}
                    refreshControl={<RefreshControl
                        refreshing={state.refreshing}
                        // onRefresh={this._onRefresh}
                        // loading={(  <img style={{width:50,height:50}} src={add}/>)}
                    />}
                />

            </div>
        );
    }
}

const mapStateToProps = state => ({
    state: state.dongtai,
})

const mapDispatchToProps = dispatch => ({
    actions: bindActionCreators(DongtaiActions, dispatch)
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Dongtai)


