import React, { Component } from 'react';
import './App.css';
import {Button} from 'antd-mobile'

class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <h2 >Welcome to React</h2>
        </div>
        <p className="App-intro">
          呵呵呵呵哇啊dsddsfdsffd
        </p>
          <Button type="primary">primary button</Button>
      </div>
    );
  }
}

export default App;



