import {
    INCREMENT_COUNTER,
    DECREMENT_COUNTER,
    REFRESH_ING,
    LOADING_ING,
    GET_ALL_TRENDS,
    LOAD_TRENDS,
} from './ActionsTypes'
import 'whatwg-fetch'
import json from './test.jsonp'
import {Toast} from 'antd-mobile'

export const increment = () => ({ type: INCREMENT_COUNTER })

export const decrement = () => ({ type: DECREMENT_COUNTER })

export const refreshIng = () => ({ type: REFRESH_ING })

export const loadIng = () => ({ type: LOADING_ING })

export const getAllTrends = (isLoad, reqPage) => {
    console.log("getAllTrends");
    console.log("isLoad-->"+isLoad);

    return (dispatch, getState) =>{

        return fetch(json)
            .then((response) => response.json())
            .then((res) => {
                if(isLoad){
                    let newRows = getState().dongtai.rows.concat(res.rows);
                    let canLoad = reqPage < getState().dongtai.maxPage;
                    console.log('LOAD_TRENDS canLoad-->'+canLoad);
                    dispatch({
                        type: LOAD_TRENDS,
                        res:  {
                            pageSize: reqPage,
                            canLoad: canLoad,
                            rows: newRows,
                            loading : false,
                        },
                    });
                }else {
                    const {total, rows} = res;
                    const {size} = getState().dongtai;
                    let maxPage = (total == 0 ? 1 : Math.ceil(total/size));
                    let canLoad = maxPage>1;
                    console.log('GET_ALL_TRENDS canLoad-->'+canLoad);

                    dispatch({
                        type: GET_ALL_TRENDS,
                        res: {
                            refreshing : false,
                            maxPage: maxPage,
                            pageSize: 1,
                            canLoad: canLoad,
                            rows: rows,
                        }
                    });
                }
            })
            .catch((error) => {
                Toast.fail(error, 1);
            })
    }
}



