import {
    INCREMENT_COUNTER,
    DECREMENT_COUNTER,
    REFRESH_ING,
    LOADING_ING,
    GET_ALL_TRENDS,
    LOAD_TRENDS,
} from '../actions/ActionsTypes'

import {ListView} from 'antd-mobile'

const initState = {
    dataSource: new ListView.DataSource({
        rowHasChanged: (row1, row2) => row1 !== row2,
    }),
    refreshing: false,
    loading: false,
    //网络请求状态
    pageSize : 1,                  // 当前页
    size : 1,                  // 请求条目数
    maxPage: 1,              // 总页数
    canLoad: false,
    rows:[
        {
            name:"df",
            content: "受到广泛大概地方刚刚获得的返回房间规划局是德国和国家的风景都是电风扇广泛化工23fffddfgdfdgfdghgf",
            type: 1,
            thumbsups: [
                {
                    name: "djy"
                },
                {
                    name: "djy2"
                },
                {
                    name: "djy3"
                }
            ],
            comment: [
                {
                    name: "djy",
                    contect: "对方的广泛大锅饭",
                },
                {
                    name: "djyerew",
                    contect: "对方的广泛大锅饭",
                },

            ],
        },
    ],
    }

export default function dongtai(state = initState, action) {
    let total, rows, pageSize, maxPage, canLoad;

    switch (action.type) {
        case REFRESH_ING:
            return Object.assign({}, state, {refreshing : true});
            break;
        case LOADING_ING:
            return Object.assign({}, state, {loading : true});
            break;
        case GET_ALL_TRENDS:
            return Object.assign({}, state, action.res);
            break;

        case LOAD_TRENDS:
            return Object.assign({}, state, action.res);
            break;

        default:
            return state;
    }
}


