import React, { Component } from 'react'

class Divider extends Component{
    render(){
        return(
            <div style={styles.divider}></div>
        );
    }
}
const styles = {
    divider: {
        marginTop: '0.15rem',
        marginBottom: '0.15rem',
        border: 'none',
        borderBottom: '1px solid #c1c1c1',
    }
}

export default Divider;


