import React, { Component } from 'react'
import './index.less'
import add from '../../../imgs/dongtai/add.png'
import Divider from '../Divider'

class DongtaiItem extends Component{

    _renderLike = ()=>{
        const {thumbsups} = this.props.item;
        console.log(thumbsups);
        let likes = '';
        if(thumbsups && thumbsups.length>0){
            thumbsups.map((item, index)=>{
                if(index < 3){
                    console.log(item.name)
                    likes += item.name;
                    if(index < 2)
                        likes += ",";
                }
            })
            if(thumbsups.length < 4)
                likes+="觉得很赞";
            else
                likes+="等"+thumbsups.length+"人觉得很赞";

            return(
                <div>
                    <Divider/>
                    <span className="font-26 color-blue likes-style">{likes}</span>
                </div>
            );
        }
    }

    _renderComment = ()=>{
        const {comment} = this.props.item;
        if(comment && comment.length>0){
            let commentViews = [];
            comment.map((item, index)=>{
                let commentView = (
                    <div key={index}>
                        <span className="font-26 color-blue" style={{marginRight:'0.12rem'}}>{item.name}:</span>
                        <span className="font-26 color-333">{item.contect}</span>
                    </div>
                )
                commentViews.push(commentView);
            })

            return(
                <div  className="like-container">
                    {commentViews}
                </div>

            );
        }
    }

    render() {
        const {item, onClick} = this.props;
        const { name, content, type, thumbsups} = item;


        return (
            <div className="container">
                <header className="top">
                    <div className="head-container">
                        <img src={add}/>
                    </div>

                    <div className="top-right">
                        <span className="font-36 color-000">{name}</span>
                        <div>
                            <span className="font-26 color-aaa">{name}</span>
                            <span className="font-26 color-aaa" style={{marginLeft: '0.12rem'}}>{type == 0? "来自好友动态" : "来自圈动态"}</span>
                        </div>
                    </div>
                </header>

                <main>
                    {/*动态内容*/}
                    <p className="font-30 color-333" style={{marginTop: '0.2rem'}}>{content}</p>
                    {/*动态赞*/}
                    {this._renderLike()}
                    {/*动态评论*/}
                    {this._renderComment()}
                </main>
            </div>
        );
    }

}

export default DongtaiItem;


